<template>
  <breadcrumb-list :items="breadcrumbs">
    <router-link
      :to="{ name: 'app.template.create' }"
      class="text-white btn bg-primary hover:bg-primary-focus focus:bg-primary-focus active:bg-primary-focus/90 dark:bg-accent dark:hover:bg-accent-focus dark:focus:bg-accent-focus dark:active:bg-accent/90"
    >
      <i class="fa fa-plus fa-sm fa-fw"></i>
      {{ $t("models.template.add") }}
    </router-link>
  </breadcrumb-list>

  <div class="grid gap-8 lg:grid-cols-2 2xl:grid-cols-3">
    <div
      v-for="template in templates"
      :key="template.id"
      class="rounded border border-slate-200 shadow-soft"
    >
      <div
        class="pointer-events-none grid gap-4 overflow-hidden p-4 h-[500px] template-preview"
      >
        <div
          v-for="(question, index) in template.questions.slice(0, 5)"
          :key="index"
          class="p-4 card"
        >
          <survey-input
            :template="template"
            :question="question"
          ></survey-input>
        </div>
      </div>

      <div
        class="flex w-full items-center justify-between rounded-b border-t border-slate-200 bg-white p-3 py-4"
      >
        <div>
          <p class="text-xl font-semibold line-clamp-1" :title="template.name">
            {{ template.name }}
          </p>
          <p class="mt-1 text-xs line-clamp-1">{{ template.description }}</p>
        </div>

        <div class="inline-flex gap-2">
          <router-link
            :to="{ name: 'app.template.edit', params: { id: template.id } }"
            :title="$t('labels.edit')"
            class="h-10 w-10 rounded-full p-0 font-medium text-white btn bg-primary hover:bg-primary-focus focus:bg-primary-focus active:bg-primary-focus/90 dark:bg-accent dark:hover:bg-accent-focus dark:focus:bg-accent-focus dark:active:bg-accent/90"
          >
            <v-icon name="pencil-square" size="5"></v-icon>
          </router-link>

          <v-button
            color="danger"
            :title="$t('labels.delete')"
            :size="10"
            icon
            class="rounded-full p-0"
            @click="deleteModal(template)"
          >
            <v-icon name="archive-delete" size="5"></v-icon>
          </v-button>

          <router-link
            :to="{ name: 'app.template.show', params: { id: template.id } }"
            :title="$t('labels.preview')"
            class="h-10 w-10 rounded-full p-0 font-medium text-white btn bg-info hover:bg-info-focus focus:bg-info-focus active:bg-info-focus/90"
          >
            <v-icon name="document-text" size="5"></v-icon>
          </router-link>
        </div>
      </div>
    </div>
  </div>

  <data-table-pagination
    :pagination="templates.pagination"
    class="card mt-5"
    @page-changed="(page) => templates.setPage(page)"
    @limit-changed="(page) => templates.setLimit(page)"
  ></data-table-pagination>

  <v-modal ref="modal">
    <template #title>{{ $t("modal.titles.delete") }}</template>
    <template #body>{{ $t("modal.messages.delete") }}</template>
    <template #buttons>
      <v-button color="danger" @click="deleteData">
        {{ $t("labels.delete") }}
      </v-button>
    </template>
  </v-modal>
</template>

<script>
import TemplateCollection from "@/collections/TemplateCollection";
import ModelRequest from "@/request/ModelRequest";
import DataTablePagination from "@/components/DataTable/DataTablePagination.vue";
import SurveyInput from "@/components/SurveyInputs/SurveyInput.vue";

export default {
  name: "TemplateList",
  components: { SurveyInput, DataTablePagination },
  data() {
    return {
      breadcrumbs: [{ title: this.$t("titles.templates"), active: true }],
      titles: [
        { column: "id", title: "#", sortable: true },
        {
          column: "user_id",
          title: this.$t("attributes.user_id"),
        },
        {
          column: "name",
          title: this.$t("attributes.template"),
          sortable: true,
        },
        {
          column: "description",
          title: this.$t("attributes.description"),
        },
        { title: this.$t("labels.actions") },
      ],
      templates: new TemplateCollection(),
      deleteSelect: null,
    };
  },
  created() {
    this.templates.fetchWithFilters();
  },
  methods: {
    deleteModal(model) {
      this.deleteSelect = model;
      this.$refs.modal.toggle();
    },
    deleteData() {
      const request = new ModelRequest(this.deleteSelect);

      request.delete().then(() => {
        this.$refs.modal.toggle();

        this.templates.fetchWithFilters();
      });
    },
  },
};
</script>